// Triple bars above needed for Jekyll processing

$header-font-stack:  Lucida Grande, Lucida Sans Unicode, Helvetica Neue, Kalimati, Calibri, Helvetica, Ariel, sans-serif;
// $sans-font-stack: Lucida Grande, Calibri, Helvetica, Ariel, sans-serif;


//Verdana, Arial, Helvetica, sans-serif;
// $sans-font-stack: Gill Sans, Gill Sans MT, Calibri, sans-serif;

$full-screen-sidenav-allotment: 200px;
$mobile-screen-sidenav-allotment: 40px;
$header-height: 50px;
$thin-space: 5px;
$med-space: 10px;
$mobile-margin: 20px;
$desktop-margin: 50px;

$cornflower: #4459e2;
$pale-cornflower: #8bb1ef;
$deep-cornflower: #224d93;
$light-gray: #ccc;
$smallscreen: 768px;

* { 
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; 
}

html { background: white; }

body { 
  font-size: 110%;
  // Body has a default margin. Override.
  font-family: $header-font-stack; 
  margin: 0px; 
}
// h1, h2, h3, h4, h5, h6 {
//   font-family: $header-font-stack; 
// }

// The left-hand bar with navigation links
.sidenav {
  height: 100%;
  width: $full-screen-sidenav-allotment;
  z-index: 10;
  position: fixed;
  top: $header-height;
  padding-top: 15px;
  left: 0;
  overflow-x: hidden;
  display: block;
  background-color: $light-gray;
  box-sizing: border-box; 
  transition: 0.3s; 
}

// An entry in the navigation menu
.entry {
 a {
    padding: 4px 8px 4px 20px;
    font-size: 120%;
    text-decoration: none;
    display: block;
    color: $cornflower;
  }

  a:hover {
      background-color: $pale-cornflower;
  }
}

.sidenav-header{
  height: $header-height + $thin-space;
  padding-left: $mobile-margin;
  font-size: 120%;
  font-weight: bold;
  line-height: $header-height;

  a {
    padding: 0;
    color: black;
    text-decoration: none;
  }

  a:visited {
    color: black;  
  }  

}

.page-header{
  height: $header-height;
  background-color: $pale-cornflower;
  font-size: 120%;
  font-weight: bold;
  line-height: $header-height;
  padding-left: $mobile-margin;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;

  a {
    padding: 0;
    color: black;
    text-decoration: none;
  }

  a:visited {
    color: black;  
  }  
}

.burger-outer{
  float: left;
  width: 30px;
  height: 100%;
  position: relative;
}

// Button containing the burger SVG
.burger-button {
  background-color: $pale-cornflower;//$light-gray;
  width: 30px;
  height: 30px;

  // Center vertically in .burger-outer
  position: absolute;
  top: 50%;
  transform: translate(0px,-50%);

  // border-radius: 6px;
  // border-style: solid;
  // border-width: 2px;
}

// Places burger svg in the middle of its containing button, left aligned.
#burger-svg {
  display: block;
  margin: auto;
  margin-left: 0px;
  height: 100%;
}

.main-body {
   // Clicks via 'onclick' make the screen flash on Safari mobile without this.
  -webkit-tap-highlight-color: transparent;
  margin-top: $header-height;
}

.page-content{

  padding-top: 1px;

  a {
    color: $cornflower;
    text-decoration: none;
  }

  a:visited {
    color: $deep-cornflower;  
  }  
}

@media (max-width: $smallscreen - 1px){
  .sidenav{ width: 0; }
  .sidenav.showing{ width: $full-screen-sidenav-allotment }
  .hide-small{display:none!important}
  .main-body {
    margin-left: 0px;//$mobile-screen-sidenav-allotment;
  }
  .page-header{
    padding-right: 5px
  }
  .page-header-title{
    padding-left: 40px;;
    width: 100%;
  }  
  .page-content{
    padding-left: $mobile-margin;
    padding-right: $mobile-margin;
  }
}

@media (min-width:$smallscreen){
  .hide-large{display:none!important}
  .sidenav{ width: $full-screen-sidenav-allotment; }
  .main-body {
    margin-left: $full-screen-sidenav-allotment; 
  }
  .page-header{
    padding-right: 20px
  }
  .page-header-title{
    padding-left: 5px;;
    width: 100%;
  }  
  .page-content{
    margin-left: $desktop-margin;
    margin-right: $desktop-margin;
  }

  // h2.top-heading{
  //     margin-top: $desktop-margin;
  // }
}

pre code {
  background-color: #eee;
  border: 2px solid #999;
  display: block;
  padding: 20px;
  overflow-x: scroll;
}

img.boxed {
  border: 2px solid black;
  overflow-x: scroll;
  vertical-align: bottom;
  display: inline-block;
  margin-top: 6px;
  margin-right: 6px;
}


table.asst-table-v2 {
  border: 2px solid black;

  border-collapse: separate;
  border-spacing: 0;

  // margin-left:30px;
  // margin-right:auto;
  // margin-top:30px;
  // margin-bottom:30px;

  th, td {
    text-align: left;
    padding: 5px;
    // padding-top: 5px;
    // padding-bottom: 5px;
    // padding-right: 5px;
  }

  th {
    vertical-align: bottom;
    padding: 5px;
  }

  div.asst-title-box{
    p {
      margin-bottom: 0px;
    }

  }
  div.due-box{
    p {
      margin-top: 4px;
    }
  }

  td.first-col {
    width:130px; 
  }

  td {
    vertical-align: top;
    padding-left: 5px;
  }

  tr:nth-child(odd) {
    background-color: #e2e2e2;
  }
  
}


dt {
  float: left;
  width: 30px;
  text-align: right;
  font-weight: bold;
}

dd {
  margin: 0 0 0 60px;
  padding: 0 0 0.5em 0;
}

div.x-scroll{
  overflow-x: scroll;  
  -webkit-overflow-scrolling: touch;  
  width: 100%;
  white-space: nowrap;
}

/* Style the buttons that are used to open and close the accordion panel */
.accordion {
  padding: 4px 8px 4px 20px;
  font-size: 120%;
  text-decoration: none;
  display: block;
  color: $cornflower;
  cursor: pointer;
  transition: 0.4s;
}

.accordion:hover {
    background-color: $pale-cornflower;
}

/* Style the accordion panel. Note: hidden by default */
.accordion-panel {
  padding: 0 18px;
  background-color: $light-gray;
  display: none;
  overflow: hidden;
}